@import "https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&family=Source+Sans+Pro:wght@400;700&family=Merriweather:wght@400;700&family=Crimson+Text:wght@400;700&display=swap";
body {
  color: #0000;
  background-color: #d1b899;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

main {
  background-color: #d1b899;
  max-width: 1200px;
  height: auto;
  margin: auto;
  padding: 1rem;
}

.tech-div {
  background-color: #d1b899;
  border-top: 2px solid #362004;
  gap: 1rem;
  height: auto;
  margin: auto;
  padding: 1rem 2rem;
  display: flex;
}

.tech-content {
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
  width: 100%;
  height: auto;
  display: grid;
}

.tech {
  border-radius: 20px;
  flex-direction: column;
  align-content: baseline;
  width: 100%;
  height: auto;
  padding: 1rem;
  transition: all .3s;
  display: grid;
}

.tech:hover .tech-photo {
  filter: none;
  border-radius: 25px;
}

.tech:hover {
  background-color: #c29e6e;
  transition: all .3s;
  transform: translateY(.5rem);
}

.tech-photo {
  object-fit: cover;
  filter: grayscale(75%);
  justify-content: center;
  width: 100%;
  height: 250px;
  padding: .5rem;
  display: flex;
}

.tech-title {
  color: #533612;
  font-family: Merriweather, serif;
  font-size: 20px;
}

p {
  color: #000;
  padding: 1rem;
  font-size: 15px;
}

@media (width <= 600px) {
  #tech-div {
    padding: 1rem;
  }

  .tech-content {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .tech-photo {
    height: 450px;
  }

  .tech-title {
    font-size: 18px;
  }

  p {
    font-size: 14px;
  }
}

@media (width >= 601px) and (width <= 900px) {
  .tech-content {
    grid-template-columns: repeat(2, 1fr);
  }

  .tech-title {
    font-size: 19px;
  }

  p {
    font-size: 15px;
  }
}

@media (width >= 901px) {
  .tech-content {
    grid-template-columns: repeat(3, 1fr);
  }

  .tech-title {
    font-size: 20px;
  }

  p {
    font-size: 15px;
  }
}
